import React, { useState, useEffect } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { AppProvider } from './context/AppContext';
import Budget from './components/Budget';
import ExpenseTotal from './components/ExpenseTotal';
import ExpenseList from './components/ExpenseList';
import AddExpenseForm from './components/AddExpenseForm';
import RemainingBudget from './components/Remaining';
import LoginPage from './components/Login';
import './components/App.css';
import './components/Login.css';
import ExpenseChart from './components/ExpenseChart';

const App = () => {
    const [isDarkMode, setIsDarkMode] = useState(false);
    const [isLoggedIn, setIsLoggedIn] = useState(false);
    const [authToken, setAuthToken] = useState(null);

    const budget = 1000;
    const expenses = [
        { id: 1, name: 'Groceries', amount: 200 },
        { id: 2, name: 'Rent', amount: 500 },
        { id: 3, name: 'Utilities', amount: 100 },
    ];

    useEffect(() => {
        const token = localStorage.getItem('authToken');
        if (token) {
            setIsLoggedIn(true);
            setAuthToken(token);
        }
    }, []);

    const toggleDarkMode = () => {
        setIsDarkMode((prevMode) => !prevMode);
    };

    const handleLogin = async (username, password) => {
        try {
            const response = await fetch('http://127.0.0.1:8000/api/token/', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ username, password }),
            });

            if (response.ok) {
                const data = await response.json();
                localStorage.setItem('authToken', data.access);
                setAuthToken(data.access);
                setIsLoggedIn(true);
            } else {
                alert('Login failed. Please check your credentials.');
            }
        } catch (error) {
            console.error('Error during login:', error);
            alert('An error occurred. Please try again later.');
        }
    };

    const handleLogout = () => {
        localStorage.removeItem('authToken');
        setAuthToken(null);
        setIsLoggedIn(false);
    };

    return (
        <div className={isDarkMode ? 'app dark-mode' : 'app'}>
            {isLoggedIn ? (
                <>
                    <header className="app-header">
                        <button onClick={toggleDarkMode} className="btn btn-secondary">
                            {isDarkMode ? 'Light Mode' : 'Dark Mode'}
                        </button>
                        <button onClick={handleLogout} className="btn btn-danger ml-2">
                            Logout
                        </button>
                    </header>
                    <AppProvider>
                        <div className="container">
                            <h1 className="mt-3">My Budget Planner</h1>
                            <div className="row mt-3">
                                <div className="col-sm">
                                    <Budget />
                                </div>
                                <div className="col-sm">
                                    <RemainingBudget />
                                </div>
                                <div className="col-sm">
                                    <ExpenseTotal />
                                </div>
                            </div>
                            <h3 className="mt-3">Expenses</h3>
                            <div className="row">
                                <div className="col-sm">
                                    <ExpenseList />
                                </div>
                            </div>
                            <h3 className="mt-3">Add Expense</h3>
                            <div className="row mt-3">
                                <div className="col-sm">
                                    <AddExpenseForm authToken={authToken} />
                                </div>
                            </div>

                            <h3 className="mt-3">Expense Overview</h3>
                            <div className="row mt-3">
                                <div className="col-sm">
                                    <ExpenseChart expenses={expenses} budget={budget} />
                                </div>
                            </div>
                        </div>
                    </AppProvider>
                </>
            ) : (
                <LoginPage onLogin={handleLogin} />
            )}
        </div>
    );
};

export default App;
