import React, { useContext } from 'react';
import { Pie } from 'react-chartjs-2';
import { AppContext } from '../context/AppContext';
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';

// Register necessary Chart.js components
ChartJS.register(ArcElement, Tooltip, Legend);

const ExpenseChart = () => {
  const { budget } = useContext(AppContext);
  const { expenses } = useContext(AppContext);

  console.log('Budget:', budget);
  console.log('Expenses:', expenses);

  // Ensure default values to prevent errors
  const totalExpenses = expenses ? expenses.reduce((sum, expense) => sum + expense.amount, 0) : 0;
  const remainingBudget = budget ? budget - totalExpenses : 0;

  console.log('Total Expenses:', totalExpenses);
  console.log('Remaining Budget:', remainingBudget);

  const data = {
    labels: ['Expenses', 'Remaining Budget'],
    datasets: [
      {
        data: [totalExpenses, remainingBudget],
        backgroundColor: ['#FF6384', '#36A2EB'],
        hoverBackgroundColor: ['#FF6384', '#36A2EB'],
      },
    ],
  };

  return (
    <div style={{ maxWidth: '400px', margin: 'auto' }}>
      <h3>Budget Overview</h3>
      {expenses && budget ? <Pie data={data} /> : <p>Loading data...</p>}
    </div>
  );
};

export default ExpenseChart;
